[data-theme='light'] {
    --transformation: -1.5em;
  }

  [data-theme='dark'] {
    --transformation: 1.5em;
  }

.switch {
    position: relative;
    display: inline-block;
    width: 3.9em;
    height: 2.1em;
    margin-right: 3em;
    
    
    & input{
        opacity: 0;
        width: 0;
        height: 0;
        
        &:checked + .slider{
            background-color: var(--accent);
            
         }

        &:checked + .slider::before{
            transform: translateX(1.5em);
        }
    } 

    & .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transition: .4s;
        border-radius: 1.5em;
        border: 1px solid #394E61;

            &::before{
                position: absolute;
                content: '';
                height: 1.5em;
                width: 1.5em;
                left: 4px;
                bottom: 4px;
                background-color: #394E61;
                transition: .4s;
                border-radius: 50%;
            }       
    }
}