[data-theme='light'] {
    --navBg: #273744;
  }

  [data-theme='dark'] {
    --navBg: #394E61;
  }

$navigationBg:var(--navBg);

nav {
    width: 100%;
    height: 4em;


        & .list {
        list-style-type: none;
        background-color:$navigationBg;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        & .items {
            margin-right: 1.25em;
            font-size: 1.25em;
            text-transform: uppercase;
            color: #f1f1f1;
            cursor: pointer;
        }
    }

    & .btn {
    display: none;
    position: absolute;
    right: 10px;
    top: 2.5em;
    padding: 5px;
    color: #000;
    font-size: 18px;
}
}


@media screen and (max-width: 800px){
    .list {
        flex-direction: column;
        height: auto;
   
    & .items:nth-child(1){
        border-top: 1px solid rgba(255, 255, 255, 0.555);
        margin-top: 50px;
    }
    & .items {
        width: 100%;
        border-top: 1px solid rgba(255, 255, 255, 0.555);
        text-align: center;
        margin-right: 0px;
        padding: 20px 0;
    }
}
    .btn {
        display: block;
    }
}

