@font-face {
    font-family: 'Poppins';
    src: url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap");
}

[data-theme='light'] {
    --navItemColor: #F4F5FB;
  }

  [data-theme='dark'] {
    --navItemColor: #273744
  }

body {
    font-family: 'Poppins', sans-serif;
}

*{
    margin: 0;
    padding: 0;
}

.social-media {

    & span{
        border: 2px solid #394E61;
        margin: 5px;
        padding: 5px;
        border-radius: 50%;
    }
}

//navbar styles
.navbar-container{
    display: block;
    max-width: 100%;

    & .navbar-logo{
        color: white;
        font-size: 2em;
        margin-left: 2em;
    }

    & .nav-item{
        text-transform: uppercase;
    }

}